}
a:hover {
  color: #7b7b7b;
}

p {
  color: #000000;
  font: normal 16px / normal "Roboto", sans-serif;
}

.button,
button,
input[type=submit],
input[type=reset] {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
  font: normal 16px / normal "Roboto", sans-serif;
  min-height: 44px;
  max-width: 100%;
  padding: 0 20px;
  margin-bottom: 10px;
  border: 1px solid #868686;
  border-radius: 3px;
  background-color: #868686;
  color: #ffffff;
}
.button:hover,
button:hover,
input[type=submit]:hover,
input[type=reset]:hover {
  background-color: #7b7b7b;
  color: #ffffff;
}
.button.full,
button.full,
input[type=submit].full,
input[type=reset].full {
  width: 100%;
}

input[type=text],
input[type=email],
input[type=url],
input[type=tel],
input[type=number],
input[type=password],
textarea,
select {
  min-height: 44px;
  max-width: 100%;
  padding: 5px 10px;
  margin-bottom: 10px;
  font: normal 16px / normal "Roboto", sans-serif;
  border: 1px solid #868686;
  border-radius: 3px;
  background-color: #ffffff;
  color: #000000;
}
input[type=text]::placeholder,
input[type=email]::placeholder,
input[type=url]::placeholder,
input[type=tel]::placeholder,
input[type=number]::placeholder,
input[type=password]::placeholder,
textarea::placeholder,
select::placeholder {
  color: #7b7b7b;
}
input[type=text].large,
input[type=email].large,
input[type=url].large,
input[type=tel].large,
input[type=number].large,
input[type=password].large,
textarea.large,
select.large {
  font-size: 24px;
  padding: 10px 20px;
}
input[type=text].full,
input[type=email].full,
input[type=url].full,
input[type=tel].full,
input[type=number].full,
input[type=password].full,
textarea.full,
select.full {
  width: 100%;
}

textarea {
  height: auto;
}

select {
  cursor: pointer;
}

.disabled,
[disabled] {
  opacity: 0.5;
  pointer-events: none;
  user-select: none;
  cursor: not-allowed;
}

h1,
.h1-size,
h2,
.h2-size,
h3,
.h3-size,
h4,
.h4-size,
h5,
.h5-size,
h6,
.h6-size {
  color: #000000;
  font: bold 20px / normal "Open Sans", serif;
}

h2,
.h2-size {
  font-size: 18px;
  line-height: normal;
  font-weight: bold;
}

h3,
.h3-size {
  font-size: 16px;
  line-height: normal;
  font-weight: normal;
}

h4,
.h4-size {
  font-size: 14px;
  line-height: normal;
  font-weight: normal;
}

h5,
.h5-size {
  font-size: 12px;
  line-height: normal;
  font-weight: normal;
}

h6,
.h6-size {
  font-size: 10px;
  line-height: normal;
  font-weight: normal;
}

figure {
  margin: 0;
}
figure > img {
  max-width: 100%;
}

main {
  min-height: calc(100vh - 200px);
}
main > section {
  min-height: 400px;
}
main > section ul > li {
  color: #000000;
  font: normal 16px / normal "Roboto", sans-serif;
}

.text-color-dark {
  color: #868686;
}
.text-color-darker {
  color: #333333;
}
.text-color-darkest {
  color: #000000;
}
.text-color-light {
  color: #7b7b7b;
}
.text-color-lighter {
  color: #eeeeee;
}
.text-color-lightest {
  color: #ffffff;
}
.text-transform-capitalize {
  text-transform: capitalize;
}
.text-transform-uppercase {
  text-transform: uppercase;
}

.font-primary {
  font-family: "Roboto", sans-serif;
}
.font-secondary {
  font-family: "Open Sans", serif;
}
.font-size-smaller {
  font-size: 12px;
}
.font-size-small {
  font-size: 14px;
}
.font-size-large {
  font-size: 18px;
}
.font-size-larger {
  font-size: 20px;
}
.font-size-largest {
  font-size: 24px;
}

.popup {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 500ms;
}
.popup.active {
  opacity: 1;
  pointer-events: all;
}
.popup > .overlay {
  position: absolute;
  z-index: 4;
  top: inherit;
  left: inherit;
  right: inherit;
  bottom: inherit;
  width: inherit;
  height: inherit;
  background-color: rgba(0, 0, 0, 0.5);
}
.popup > .wrap {
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  z-index: 5;
}
.popup > .wrap > .col {
  background-color: #ffffff;
}
.popup .close-button {
  position: absolute;
  z-index: 6;
  top: 10px;
  right: 20px;
}
.popup .close-button > a {
  text-decoration: none;
  font-size: 50px;
  line-height: 50px;
  color: #ffffff;
  text-shadow: -1px 0 #000000, 0 1px #000000, 1px 0 #000000, 0 -1px #000000;
}

.video-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
}
.video-wrapper > video {
  max-width: 100%;
  display: block;
  height: auto;
  cursor: pointer;
  transition: 500ms;
}
.video-wrapper > video.fill {
  width: 100%;
  object-fit: cover;
}
.video-wrapper > .video-content, .video-wrapper > .video-text, .video-wrapper > .play-button, .video-wrapper > .pause-button {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 500ms;
}
.video-wrapper > .video-content.video-content, .video-wrapper > .video-text.video-content, .video-wrapper > .play-button.video-content, .video-wrapper > .pause-button.video-content {
  top: 0;
  left: 0;
  width: 100%;
  transform: none;
}
.video-wrapper > .video-content.play-button, .video-wrapper > .video-content.pause-button, .video-wrapper > .video-text.play-button, .video-wrapper > .video-text.pause-button, .video-wrapper > .play-button.play-button, .video-wrapper > .play-button.pause-button, .video-wrapper > .pause-button.play-button, .video-wrapper > .pause-button.pause-button {
  background-color: #ffffff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.video-wrapper > .pause-button {
  opacity: 0;
  pointer-events: none;
}
.video-wrapper.full {
  width: 100%;
}
.video-wrapper.full > video {
  width: 100%;
}
.video-wrapper.bg-video > video {
  width: 100%;
  object-fit: cover;
  cursor: default;
}
.video-wrapper:not(.playing):not(.bg-video) > video[autoplay][muted] {
  opacity: 0.6;
}
.video-wrapper.playing > .play-button {
  opacity: 0;
  pointer-events: none;
}
.video-wrapper.playing:hover > .pause-button {
  opacity: 1;
  pointer-events: all;
}

.screen-reader-text {
  position: absolute;
  margin: -1px;
  padding: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  word-wrap: normal !important;
}

.xl-middle.xl-between {
  align-content: initial;
}

